.article-container {
    background-color: #fff;
    width: 622px;
    height: auto;
    margin: 0 13px 17px 0;
  
    h2 {
      width: auto;
      height: auto;
      font-family: "Bitter", serif;
      font-size: 24px;
      font-weight: 700;
      padding: 15px 31px;
    }
  }
  
  .article-box {
    border-bottom: 2px solid #ddd;
  }
  
  .article-heading {
    font-family: "Bitter", serif;
    font-size: 24px;
    font-weight: 700;
    line-height: 28px;
    width: 496px;
    height: auto;
    margin: 21px 0 16px 31px;
  }
  
  .article-info {
    display: flex;
    justify-content: start;
    align-items: center;
    font-family: "Varela Round", sans-serif;
    font-weight: 400;
    line-height: 14.45px;
    font-size: 12px;
    color: #666;
    padding-left: 31px;
  
    span {
      padding-right: 40px;
    }
  }
  
  .article {
    display: flex;
  
    img {
      width: 170px;
      height: 128px;
      margin: 16px 20px auto 31px;
      object-fit: cover;
      background-size: cover;
    }
  
    p {
      width: 383px;
      height: 176px;
      font-family: "Bitter", serif;
      font-size: 14px;
      line-height: 22px;
      font-weight: 400;
      color: #444;
      margin-top: 7px;
    }
  }
  
  .article-box button {
    display: block;
    width: 123px;
    height: 33px;
    background-color: #4d4382;
    color: #fff;
    font-family: "Varela Round", sans-serif;
    border: none;
    font-size: 14px;
    margin-bottom: 40px;
    margin-left: 220px;
  }
  
  .pages {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    margin-top: 32px;
    padding-bottom: 31px;
    padding-left: 30px;
  
    a {
      background-color: #ddd;
      color: #4d4382;
      text-decoration: none;
      padding: 8px 7px 8px 12px;
      font-family: "Varela Round", sans-serif;
      font-size: 14px;
      margin: 3px;
      width: 32px;
      height: 32px;
  
      &:hover {
        color: #fff;
        background-color: #4d4382;
      }
    }
  }
  
  @media (max-width: 640px) {
    .arrow-right {
      margin-top: 170px;
    }
  
    .article-container {
      width: 100%;
      height: auto;
    }
  
    .article-box p {
      height: auto;
      width: auto;
      padding: 10px;
    }
  
    .article-heading {
      width: auto;
      font-size: 18px;
    }
  
    .article {
      flex-direction: column;
      justify-content: center;
      align-items: center;
  
      img {
        width: 100%;
        height: 100%;
      }
    }
  
    .article-box button {
      display: block;
      margin-top: 50px;
      margin-left: 190px;
    }
  }